.header-image{
    max-height: 60vh;
    overflow: hidden;
    img{
        display: block;
        width: 100vw;
    }
}

.site-header{
    position: relative;
    padding: 1em;
    font-family: $font__sans;
    color: #fff;
    background-color: $color__skin;

    @media screen and (min-width: $query_small){
        padding: 1em 2em;
    }

    @media screen and (min-width: $query_medium) {
        display: flex;
        justify-content: space-between;
    }
}

.site-branding{
    display: flex;
    min-height: 65px;
    &__text{
        display: flex;
        flex-direction: column;
        justify-content: center;
        height: 65px;
    }
     @media screen and (min-width: $query_medium) {
        flex: 1 0 auto;
        width: 35%;
    }

    .custom-logo-link{
        margin-right: 1em;
        img{
            display: block;
            height: 65px;
            width: auto;
        }
    }
}

.site-navigation{
    @media screen and (min-width: $query_medium) {
        width: 75%;
    }
}

.site-title{
    margin: 0;
    padding: 0;
    font-size: 1.6em;
    font-weight: 900;
    line-height:1em;
    a{
        color: white;
        text-decoration: none;
        &:hover, &:focus{
            text-decoration: underline;
        }
    }
}

.site-description{
    margin: 0;
    font-size: .9em;
    font-style: italic;
    font-weight: 100;
}

